.intro {
  display: flex;
}

.intro-right {
  flex: 1;
  position: relative;
}

.intro-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-left-wrapper {
  padding: 50px;
  /* height: 50%; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}

.i-name {
  font-size: 60px;
}

.i-title {
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25%{
    transform: translateY(-50px);
  }
  50%{
    transform: translateY(-100px);
  }
  75%{
    transform: translateY(-150px);
  }
  100%{
    transform: translateY(-200px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #59b256;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 300px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: absolute;
  pointer-events: none;
}

.i-bg {
  clip-path: polygon(100% 0%, 100% 49%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #59b256;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
}
/* flexbox display: flex align-items: center can be used to center a div with only text inside  */

.i-desc {
  font-size: 1.5rem;
}

@media (prefers-reduced-motion: no-preference) {
  .i-img {
    animation: i-img-spin infinite 40s linear;
  }
}

@keyframes i-img-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 480px) {
  .intro{
    flex-direction: column;
  }

  .i-left-wrapper {
    padding: 10px;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
  }

  .i-desc {
    display: none;
  }

  .i-title-item {
    justify-content: center;
  }

  .i-desc {
    font-size: 1rem;
  }
}

@media screen and (max-width: 830px) {
  .i-title-wrapper{
    width: max-content;
  }
}
